import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        '& > *': {
            margin: theme.spacing(1),
            width: theme.spacing(40),
            height: theme.spacing(40),
        },
    },
}));

export default function Footerpaper() {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            {/* <Card /> */}
            <Card className={classes.root}>
                <CardContent>
                    <Typography variant="h7" component="h3">
                        IRDAI Notice on Spurious Calls
                    </Typography>
                    <Typography variant="h9" component="h6">
                        BEWARE OF SPURIOUS PHONE CALLS AND FICTITIOUS/FRAUDULANT OFFERS
                    </Typography>
                    <Typography variant="h9" component="h6">
                        IRDAI is not involved in activities like selling insutance policies, 
                        announcinng bonus or investment of premiums.Public receiving such phone 
                        calls are requested to lodge a police complaint.
                    </Typography>
                    <Divider />
                    <Typography variant="h7" component="h3">
                        Copyright. All rights reserved
                    </Typography>
                    <ButtonGroup />
                    <ButtonGroup variant="text" color="primary" aria-label="text primary button group">
                        <Button>Privacy Policy</Button>
                        <Button>Terms of Use</Button>
                        <Button>Sales Policy</Button>
                        <Button>Legal</Button>
                    </ButtonGroup>
                </CardContent>
            </Card>
        </div>
    );
}
