import './App.css';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Home from './Pages/Home';
import ProminentAppBar from './Components/appbar';
import Compareplans1 from './Pages/Compareplans1';
import Compareplans2 from './Pages/Compareplans2';
import Compareplans3 from './Pages/Compareplans3';


function App() {
  return (
    <div className="App">
      {/* Header (Navigation Bar) */}
      <header className="App-header">
      <ProminentAppBar />
      </header>
      
      {/* Router - Pages */}
          <Switch>
            <Route path={process.env.PUBLIC_URL + '/'} exact > <Home/> </Route>

            <Route path={process.env.PUBLIC_URL + '/home'} > <Home/> </Route>
            <Route path={process.env.PUBLIC_URL + '/Compareplans1'} > <Compareplans1/> </Route>
            <Route path={process.env.PUBLIC_URL + '/Compareplans2'} > <Compareplans2/> </Route>
            <Route path={process.env.PUBLIC_URL + '/Compareplans3'} > <Compareplans3/> </Route>
          </Switch>
          
      {/* <footer className="App-footer">
        This is a footer
      </footer> */}
    </div>
  );
  }

export default App;
