import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import HelpIcon from '@material-ui/icons/Help';

const useStyles = makeStyles({
    root: {
        minWidth: 275,
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        fontSize: 14,
    },
    pos: {
        marginBottom: 12,
    },
});

export default function Card1() {
    const classes = useStyles();
    const bull = <span className={classes.bullet}>•</span>;

    return (
        <Card className={classes.root}>
            <CardContent>
                <CardActions>
                    <HelpIcon /> <Button size="small">What is the clain settlement ratio and reasons for claim repudiation?</Button>
                </CardActions>
                <CardActions>
                    <HelpIcon /> <Button size="small">How do I know about my ideal sum assured?</Button>
                </CardActions>
                <CardActions>
                    <HelpIcon /> <Button size="small">How much life insurance cover do I need?</Button>
                </CardActions>
                <CardActions>
                    <HelpIcon /> <Button size="small">what are the benefits of buying life insurance?</Button>
                </CardActions>
            </CardContent>
            <CardActions>
        <Button size="small">Read More</Button>
      </CardActions>
        </Card>
    );
}
