import React from 'react';
import Container from '@material-ui/core/Container';
import SimplePaper from '../Components/Homepage/paper';
import axaguy from '../Assets/axaguy.svg';
import SimpleCard from '../Components/Homepage/simplecard';
import Trendingnow from '../Components/Homepage/paper2';
import TextMobileStepper from '../Components/Homepage/carousel';
import TextMobileStepper1 from '../Components/Homepage/carousel1';
import Queries from '../Components/Homepage/paper3';
import Card1 from '../Components/Homepage/card1';
import Quote from '../Components/Homepage/card2';
import IconButtons from '../Components/Footer/socialmedia';
import IconLabelButtons from '../Components/Footer/contacticons';
import ControlledAccordions from '../Components/Footer/accordian';
import Footerpaper from '../Components/Footer/paper';


export default function Home() {
  
    return (
        <React.Fragment>
        <Container fixed>
        </Container>
        <SimplePaper />
        <img src= {axaguy} className= "axaguy" alt= "axaguy" width="350" height="300"/>
        <SimpleCard />
        <Trendingnow />
        <TextMobileStepper />
        <TextMobileStepper1 />
        <Queries />
        <Card1 />
        <Quote />
        <IconButtons /> 
        <IconLabelButtons />
        <ControlledAccordions />
        <Footerpaper />
        
       
    </React.Fragment>
    
    
    
 );
}