import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import SimpleCarda from './ComparePlans2/plancard2a';
import SimpleCardb from './ComparePlans2/plancard2b';
import SimpleCardc from './ComparePlans2/plancard2c';
import SimpleCardd from './ComparePlans2/plancardd';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    height: 140,
    width: 100,
  },
  control: {
    padding: theme.spacing(2),
  },
}));

// Spacing Grid for Compare Plans page 2 (To show all available investment plans) //

export default function SpacingGrid() {
  const [spacing, setSpacing] = React.useState(2);
  const classes = useStyles();

  const handleChange = (event) => {
    setSpacing(Number(event.target.value));
  };

  return (
    <Grid container className={classes.root} spacing={2}>
      <Grid item xs={12} sm={12} md={12}>
        <SimpleCarda />
      </Grid>
      <Grid item xs={12} sm={12} md={12}>
        <SimpleCardb />
      </Grid>
      <Grid item xs={12} sm={12} md={12}>
        <SimpleCardc />
      </Grid>
      <Grid item xs={12} sm={12} md={12}>
        <SimpleCardd />
      </Grid>
    </Grid>
  );
}
